.featuresWrapper {
    display: flex;
    justify-content: center;
    margin: 20px;
}

.municipalFeaturesHeading {
    margin-top: 20px;
}

.customersFeaturesHeading, .municipalFeaturesHeading {
    color: var(--pri-blue);
}

.paymentMode img, .calender img, .alarmSystem img, .complaints img, .geoLocation img, .easyWork img, .attendance img, .visibility img {
    height: 50px;
    display: none;
}

.featuresWrapper .globe {
    width: 100%;
    display: none;
}

.listItem::before {
    content: '';
    width: 40px;
    height: 40px;
    position: absolute;
    background-size: cover;
    background-position: center;
    top: 50%;
    left: -43px;
    transform: translateY(-50%);
}

.paymentListItem::before {
    background-image: url('../../assets/Images/features/mobile_bullet1.svg');
}

.calenderListItem::before {
    background-image: url('../../assets/Images/features/mobile_bullet2.svg');
}

.alarmListItem::before {
    background-image: url('../../assets/Images/features/mobile_bullet3.svg');
}

.complaintsListItem::before {
    background-image: url('../../assets/Images/features/mobile_bullet4.svg');
}

.geoListItem::before {
    background-image: url('../../assets/Images/features/mobile_bullet5.svg');
}

.easyWorkListItem::before {
    background-image: url('../../assets/Images/features/mobile_bullet6.svg');
}

.attendanceListItem::before {
    background-image: url('../../assets/Images/features/mobile_bullet7.svg');
}

.visibilityListItem::before {
    background-image: url('../../assets/Images/features/mobile_bullet8.svg');
}

.listItem {
    position: relative;
    margin: 30px 0px 0px 43px;
}

@media only screen and (min-width: 1024px) {
    .listItem::before {
        display: none;
    }
    .municipalFeaturesHeading {
        margin-top: 0;
    }
    .featuresWrapper {
        padding: 120px 0px;
    }
    .featuresWrapper .globe {
        display: block;
    }
    .globeWrapper.desktop {
        display: block;
        width: 200px;
        position: relative;
    }
    .paymentMode img, .calender img, .alarmSystem img, .complaints img, .geoLocation img, .easyWork img, .attendance img, .visibility img {
        display: block;
    }
    .customersFeaturesHeading {
        top: -90px;
        left: -415px;
    }
    .paymentMode {
        top: -40px;
        left: -400px;
    }
    .calender {
        top: 30px;
        left: -415px;
    }
    .alarmSystem {
        top: 100px;
        left: -415px;
    }
    .complaints {
        top: 170px;
        left: -400px;
    }
    .municipalFeaturesHeading {
        top: -90px;
        right: -415px;
    }
    .geoLocation {
        top: -40px;
        right: -400px;
    }
    .easyWork {
        top: 30px;
        right: -415px;
    }
    .attendance {
        top: 100px;
        right: -415px;
    }
    .visibility {
        top: 170px;
        right: -400px;
    }
    .paymentMode, .calender, .alarmSystem, .complaints, .geoLocation, .easyWork, .attendance, .visibility, .customersFeaturesHeading, .municipalFeaturesHeading {
        display: flex;
        position: absolute;
        width: 400px;
        align-items: center;
    }
}

@media only screen and (min-width: 1200px) {
    .globeWrapper.desktop {
        width: 250px;
    }
    .paymentMode {
        top: -35px;
        left: -400px;
    }
    .calender {
        top: 45px;
        left: -425px;
    }
    .alarmSystem {
        top: 125px;
        left: -425px;
    }
    .complaints {
        top: 205px;
        left: -400px;
    }
    .geoLocation {
        top: -35px;
        right: -400px;
    }
    .easyWork {
        top: 45px;
        right: -425px;
    }
    .attendance {
        top: 125px;
        right: -425px;
    }
    .visibility {
        top: 205px;
        right: -400px;
    }
}